/* --- About Page ------------------------------------------------------------------------------------ */

@keyframes fade-in {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}

@keyframes fade-in-with-drop {
    from {
      opacity: 0;
      transform: translateY(-50px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
}

.fade-in-animation {
    animation: fade-in 1s ease-in-out;
}

.fade-in-text-second {
    animation-fill-mode: forwards;
    animation-delay: 1s;
    opacity: 0;
}

.fade-in-skill {
    animation: fade-in-with-drop 1s ease-in-out;
    animation-fill-mode: forwards;
    opacity: 0;
}

h2, h3 {
    color: white
}

.about-page {
    margin: auto;
    width: 50%;
}

.skillBubble {
    display: inline-block;
    background-color: rgb(230, 230, 230);
    color: black;
    font-family: Arial, Helvetica, sans-serif !important;
    /* font-weight: bold; */
    font-size: 20px;
    height: 40px;
    width: 100px;
    margin: 5px;
    border-radius: 10px;
    line-height: 40px;
    vertical-align: middle;
    text-align: center;
}

.redirectBtn {
    display: inline-block;
    background-color: rgb(40, 193, 248);
    color: white;
    font-family: Arial, Helvetica, sans-serif !important;
    font-size: 17px;
    width: 140px;
    height: 40px;
    border: none;
    border-radius: 10px;
    margin: 5px;
    line-height: 40px;
    vertical-align: middle;
    text-align: center;
}


/* --- Project Page ------------------------------------------------------------------------------------ */

.project-page{
    /* background-color: black; */
    /* height: 100vh; */
    width: 80%;
}

#image-track {
    display: flex;
    gap: 4vmin;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(0%, -50%);
    margin: 0rem;
    overflow: hidden;
}

#image-track > .image {
    width: 40vmin;
    height: 56vmin;
    object-fit: cover;
    object-position: 100% 50%;
    border-radius: 10px;
    /* box-shadow: 0 5px 5px rgba(255, 255, 255, 0.2); */
}

.no-select {
    user-select: none;
  }

.fullscreen-overlay {
    position: fixed;
    top: 1;
    left: 0;
    width: 100%;
    /* margin-top: 100px; */
    /* margin-left: 50px; */
    /* height: 100%; */
    /* background-color: rgba(0, 0, 0, 0.8); */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999; 
    margin: 40px;
}

.fullscreen-image {
    width: 1200px;
    height: 600px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.8);
}

@keyframes pointLeft {
    from { -webkit-transform: translateX(0); opacity: 0.4 }
    to { -webkit-transform: translateX(0.4em); opacity: 0.9 }
}

.left {
    animation: pointLeft 0.6s infinite alternate ease-in-out;
    border-bottom:2em solid #00b6f1;
}

.arrowContainer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.leftArrow {
    width: 0;
    height: 0;
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
    border-right: 20px solid white;
    opacity: 0.8;
}